@font-face {
  font-family: "PlayfairDisplay";
  src: url("../fonts/PlayfairDisplay-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "PTSerif";
  src: url("../fonts/PTSerif-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

:root {
  --clr-light: #fff;
  --clr-main: #000;
  --radius: 4px;
  --ms: 0.3s;
}

html {
  scroll-behavior: smooth;
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

html,
body {
  height: 100%;
}

body {
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

aside,
nav,
footer,
header,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  vertical-align: top;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  /* 2 */
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
}

button,
input {
  /* 1 */
  overflow: visible;
}

button,
select {
  /* 1 */
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
  background-color: transparent;
  cursor: pointer;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

input::-ms-clear {
  display: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

ul li {
  list-style: none;
}

body {
  font-family: "PTSerif", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: var(--clr-main);
}

body.lock {
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1750px;
}

@keyframes arrow-bounce {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-30%);
  }

  100% {
    transform: translateY(0);
  }
}

.btn-primary {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  border-radius: var(--radius);
  width: 100%;
  height: 60px;
  max-width: 300px;
  background-color: var(--clr-main);
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  width: 100%;
  height: 80px;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  width: 100%;
  max-width: 97px;
  max-height: 65px;
}

.header__logo img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.header__nav-list {
  display: flex;
  align-items: center;
}

.header__nav-item:not(:last-child) {
  margin-right: 30px;
}

.header__nav-link {
  position: relative;
  padding-top: 29px;
  padding-bottom: 29px;
}

.header__cart svg {
  width: 44px;
  height: 44px;
}

.header__burger {
  display: none;
}

.main {
  position: relative;
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  min-height: 500px;
}

.main__background {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.main__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main__background::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

.main__wrapper {
  margin-right: auto;
  margin-left: auto;
  max-width: 650px;
  text-align: center;
  color: var(--clr-light);
}

.main__title {
  font-size: 56px;
  margin-bottom: 25px;
  font-family: "PlayfairDisplay", "PTSerif", sans-serif;
}

.main__subtitle {
  margin-right: auto;
  margin-bottom: 25px;
  margin-left: auto;
  max-width: 520px;
  line-height: 130%;
}

.main__arrow {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  animation-name: arrow-bounce;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

.main__arrow svg {
  width: 16px;
  height: 32px;
  fill: var(--clr-light);
  opacity: 0.7;
  transition: opacity 0.3s;
}

@media (min-width: 992px) {
  .header__cart:focus {
    outline: none;
    box-shadow: 0 0 20px var(--clr-main);
  }

  .main__arrow:hover svg,
  .main__arrow:focus svg {
    opacity: 1;
    transition: opacity 0.3s;
  }
}

@media (min-width: 1101px) {
  .header__nav-link::after {
    content: "";
    position: absolute;
    left: -10%;
    bottom: 28px;
    z-index: -1;
    width: 120%;
    height: 0;
    background-color: var(--clr-light);
    transition: height var(--ms);
  }

  .header__nav-link:hover::after,
  .header__nav-link:focus::after {
    height: 10px;
    transition: height var(--ms);
  }
}

@media (max-width: 1750px) {
  .main__title {
    font-size: calc(36px + 20 * ((100vw - 320px) / 1430));
  }
}

@media (max-width: 1100px) {
  .header__nav {
    position: fixed;
    left: -100%;
    top: 0;
    z-index: 5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: var(--clr-main);
    transition: left calc(var(--ms) * 3) cubic-bezier(0.645, 0.045, 0.355, 1) calc(var(--ms) * 2);
  }

  .header__nav--active {
    left: 0;
    transition: left calc(var(--ms) * 3) cubic-bezier(0.645, 0.045, 0.355, 1);
  }

  .header__nav--active .header__nav-link {
    opacity: 1;
    transform: translateY(0);
    transition: transform calc(var(--ms) * 2) cubic-bezier(0.645, 0.045, 0.355, 1) calc(var(--ms) * 3), opacity calc(var(--ms) * 2) ease-in-out calc(var(--ms) * 3);
  }

  .header__nav-list {
    display: block;
    overflow: auto;
    padding: 50px 15px;
    text-align: center;
  }

  .header__nav-item {
    overflow: hidden;
  }

  .header__nav-item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .header__nav-link {
    font-size: 40px;
    display: inline-block;
    padding-top: 0;
    padding-bottom: 0;
    text-transform: uppercase;
    color: var(--clr-light);
    opacity: 0;
    transform: translateY(100%);
    transition: calc(var(--ms) * 2) cubic-bezier(0.645, 0.045, 0.355, 1);
  }

  .header__burger {
    position: relative;
    z-index: 15;
    display: block;
    width: 40px;
    height: 25px;
    background-color: transparent;
  }

  .header__burger:focus {
    outline: none;
  }

  .header__burger::after,
  .header__burger::before,
  .header__burger span {
    content: "";
    position: absolute;
    left: 0;
    border-radius: 8px;
    width: 100%;
    height: 4px;
    background-color: var(--clr-main);
    transition: background-color calc(var(--ms) * 2);
  }

  .header__burger::before {
    top: 0;
    transition: transform calc(var(--ms) * 2), calc(var(--ms) * 2);
  }

  .header__burger::after {
    bottom: 0;
    transition: transform calc(var(--ms) * 2), bottom calc(var(--ms) * 2);
  }

  .header__burger span {
    top: 11px;
    transition: transform calc(var(--ms) * 2);
  }

  .header__burger--active::after,
  .header__burger--active::before,
  .header__burger--active span {
    background-color: var(--clr-light);
    transition: background-color calc(var(--ms) * 2);
  }

  .header__burger--active span {
    transform: scale(0);
    transition: transform calc(var(--ms) * 2);
  }

  .header__burger--active::before {
    top: 11px;
    transform: rotate(45deg);
    transition: transform calc(var(--ms) * 2), top calc(var(--ms) * 2);
  }

  .header__burger--active::after {
    bottom: 10px;
    transform: rotate(-45deg);
    transition: transform calc(var(--ms) * 2), bottom calc(var(--ms) * 2);
  }
}

@media (max-width: 1100px) and (max-width: 1750px) {
  .header__nav-link {
    font-size: calc(24px + 16 * ((100vw - 320px) / 1430));
  }
}

@media (max-width: 992px) {
  .header__cart:focus {
    outline: none;
  }
}